:root {
  --white: white;
  --grey: #808080;
  --wheat:#f5deb3;
  --black: #212121;
  --blue: #0000ff;
  --bluemix:#266769;
  --yellow: #fabb05;
  --green: #7eea88;
  --danger: #e41b23;
  --par: "Koho", sans-serif;
  --special: "Pattaya", sans-serif;
}
.flex-wrap {
  display: flex;
  flex-wrap: wrap; }

#next {
  margin-right: 5%;
  margin-left: 65%;
  margin-bottom: 3%;
  width: 100%;
  border-radius: 4rem; }

.signup-form {
  background-color: var(--white);
  border-radius: 1rem;
  margin-top: 5%; }

.BtnGroup {
  margin-top: 5%;
  overflow: hidden;
  width: 100%;
  margin-bottom: 10%; }

.BtnGroup :first-child {
  float: none;
  margin-left: 5%; }

.BtnGroup :nth-child(2) {
  margin-right: 5%;
  float: right; }

#next-with-previous,
#previous {
  padding: 10px 25px;
  border: none;
  color: #fff;
  cursor: pointer;
  border-radius: 4rem;
  outline: none;
  width: 30%; }

.text-field {
  margin: 5%; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

@media only screen and (min-width: 600px) {
  .signup-form {
    margin-left: 20%;
    margin-right: 20%;
    border-radius: 4rem;
    margin-top: 1%; }
  .text-field {
    margin: 0%; } }

.all-done-message {
  text-align: center;
  padding-top: 1%;
  color: var(--black); }

.header-main {
  background: var(--bluemix);
  margin: 0;
  width: 100%; }

.header-text-center {
  display: inline-flex;
  color: var(--white);
  opacity: 0.8;
  text-align: center;
  margin-left: 25%; }

.form-header {
  text-align: center;
  padding-top: 1%;
  color: var(--blue); }

@media only screen and (min-width: 600px) {
  .header-text-center {
    display: inline-flex;
    font-size: 40px;
    margin-left: 40%; } }

